import { useState, useRef, useEffect } from "react";
import { flushSync } from "react-dom";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";
import { Send, Loader2, User, Mic, MicOff } from "lucide-react";
import { useSiteSettings } from "@/hooks/useSiteSettings";

// TypeScript declarations for Web Speech API
interface SpeechRecognitionEvent extends Event {
  results: SpeechRecognitionResultList;
}

interface SpeechRecognitionErrorEvent extends Event {
  error: string;
}

interface SpeechRecognition extends EventTarget {
  lang: string;
  continuous: boolean;
  interimResults: boolean;
  onstart: ((this: SpeechRecognition, ev: Event) => void) | null;
  onresult: ((this: SpeechRecognition, ev: SpeechRecognitionEvent) => void) | null;
  onerror: ((this: SpeechRecognition, ev: SpeechRecognitionErrorEvent) => void) | null;
  onend: ((this: SpeechRecognition, ev: Event) => void) | null;
  start(): void;
  stop(): void;
}

declare global {
  interface Window {
    SpeechRecognition: new () => SpeechRecognition;
    webkitSpeechRecognition: new () => SpeechRecognition;
    // Native voice bridge for iOS/Android
    webkit?: {
      messageHandlers: {
        VoiceBridge?: {
          postMessage: (message: string) => void;
        };
      };
    };
    VoiceBridge?: {
      postMessage: (message: string) => void;
    };
    onSpeechResult?: (text: string) => void;
  }
}

// Check for native voice bridge support
const hasNativeVoiceBridge = (): boolean => {
  return !!(
    (window.webkit?.messageHandlers?.VoiceBridge) || 
    window.VoiceBridge
  );
};

// Native voice bridge utility
const NativeVoice = {
  start: () => {
    const message = JSON.stringify({ action: "start" });
    if (window.webkit?.messageHandlers?.VoiceBridge) {
      window.webkit.messageHandlers.VoiceBridge.postMessage(message);
    } else if (window.VoiceBridge) {
      window.VoiceBridge.postMessage(message);
    }
  },
  stop: () => {
    const message = JSON.stringify({ action: "stop" });
    if (window.webkit?.messageHandlers?.VoiceBridge) {
      window.webkit.messageHandlers.VoiceBridge.postMessage(message);
    } else if (window.VoiceBridge) {
      window.VoiceBridge.postMessage(message);
    }
  }
};
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { ScrollArea } from "@/components/ui/scroll-area";
import { cn } from "@/lib/utils";
import { useAuth } from "@/contexts/AuthContext";
import LocationPermissionDialog from "@/components/LocationPermissionDialog";
import ExternalLinkConfirmDialog from "@/components/ExternalLinkConfirmDialog";
import { openExternalUrl } from "@/lib/externalLinks";

interface AskXiaoxiongDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  provider?: "ask-xiaoxiong" | "xiaoxiong-agent" | "lovable-ai";
}

interface Message {
  id: string;
  role: "user" | "assistant";
  content: string;
  isStreaming?: boolean;
}

const SUPABASE_URL = import.meta.env.VITE_SUPABASE_URL;
const SUPABASE_ANON_KEY = import.meta.env.VITE_SUPABASE_PUBLISHABLE_KEY;

// Location-related keywords in multiple languages
const LOCATION_KEYWORDS = [
  // 中文
  "最近", "附近", "离我", "距离", "远近", "近的", "周边", "周围", "最近的", "附近的",
  // 英文
  "nearest", "closest", "nearby", "near me", "close to me", "distance", "around me",
  // 葡萄牙语
  "mais perto", "próximo", "perto de mim", "distância", "mais próximo"
];

// Get or create a random user ID for non-logged-in users
const getAnonymousUserId = (): string => {
  const storageKey = "xiaoxiong_anonymous_user_id";
  let anonymousId = localStorage.getItem(storageKey);
  if (!anonymousId) {
    anonymousId = `anon_${Date.now()}_${Math.random().toString(36).substring(2, 11)}`;
    localStorage.setItem(storageKey, anonymousId);
  }
  return anonymousId;
};

const AskXiaoxiongDialog = ({ open, onOpenChange, provider = "ask-xiaoxiong" }: AskXiaoxiongDialogProps) => {
  const { i18n } = useTranslation();
  const navigate = useNavigate();
  const { user } = useAuth();
  const { settings } = useSiteSettings();
  const logoUrl = settings?.logo_url;
  const [input, setInput] = useState("");
  const [messages, setMessages] = useState<Message[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [userLocation, setUserLocation] = useState<{ lat: number; lng: number } | null>(null);
  const [showLocationDialog, setShowLocationDialog] = useState(false);
  const [pendingMessage, setPendingMessage] = useState<string | null>(null);
  const [isListening, setIsListening] = useState(false);
  const [speechSupported, setSpeechSupported] = useState(false);
  const [nativeVoiceSupported, setNativeVoiceSupported] = useState(false);
  
  const [externalLinkDialogOpen, setExternalLinkDialogOpen] = useState(false);
  const [pendingExternalLink, setPendingExternalLink] = useState<string | null>(null);
  const abortControllerRef = useRef<AbortController | null>(null);
  const scrollAreaRef = useRef<HTMLDivElement>(null);
  const recognitionRef = useRef<SpeechRecognition | null>(null);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  // Determine if voice input should be shown
  const shouldShowVoiceInput = (): boolean => {
    return speechSupported || nativeVoiceSupported;
  };

  // Get user ID for agent provider
  const getUserId = (): string => {
    if (user?.id) {
      return user.id;
    }
    return getAnonymousUserId();
  };

  // Check for speech recognition support (Web Speech API and native bridge)
  useEffect(() => {
    const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
    setSpeechSupported(!!SpeechRecognition);
    setNativeVoiceSupported(hasNativeVoiceBridge());
    
    // Register global callback for native voice recognition
    window.onSpeechResult = (text: string) => {
      setInput(text);
      setIsListening(false);
    };
    
    return () => {
      // Cleanup global callback
      window.onSpeechResult = undefined;
    };
  }, []);


  // Get user location when dialog opens
  useEffect(() => {
    if (open && !userLocation) {
      navigator.geolocation?.getCurrentPosition(
        (position) => {
          setUserLocation({
            lat: position.coords.latitude,
            lng: position.coords.longitude,
          });
        },
        (error) => {
          console.log("Unable to get user location:", error.message);
        },
        { enableHighAccuracy: false, timeout: 5000, maximumAge: 300000 }
      );
    }
  }, [open, userLocation]);

  // Cleanup speech recognition when dialog closes
  useEffect(() => {
    if (!open && recognitionRef.current) {
      recognitionRef.current.stop();
      setIsListening(false);
    }
  }, [open]);

  // Auto scroll to bottom when new messages arrive
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  const handleSubmitAskXiaoxiong = async (userMessage: Message, assistantMessageId: string) => {
    const response = await fetch(`${SUPABASE_URL}/functions/v1/ask-xiaoxiong`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${SUPABASE_ANON_KEY}`,
        'apikey': SUPABASE_ANON_KEY,
      },
      body: JSON.stringify({ 
        text: userMessage.content, 
        stream: true,
        lat: userLocation?.lat,
        lng: userLocation?.lng,
      }),
      signal: abortControllerRef.current?.signal,
    });

    if (!response.ok) {
      throw new Error(`Request failed with status: ${response.status}`);
    }

    if (!response.body) {
      throw new Error('No response body');
    }

    const reader = response.body.getReader();
    const decoder = new TextDecoder();
    let accumulatedResult = "";

    while (true) {
      const { done, value } = await reader.read();
      
      if (done) {
        break;
      }

      const chunk = decoder.decode(value, { stream: true });
      accumulatedResult += chunk;
      
      // Clean up the result for display
      let cleanResult = accumulatedResult;
      if (cleanResult.startsWith('"')) {
        cleanResult = cleanResult.slice(1);
      }
      if (cleanResult.endsWith('"')) {
        cleanResult = cleanResult.slice(0, -1);
      }
      cleanResult = cleanResult.replace(/\\n/g, '\n');
      
      // Update the assistant message
      setMessages((prev) =>
        prev.map((msg) =>
          msg.id === assistantMessageId
            ? { ...msg, content: cleanResult }
            : msg
        )
      );
    }

    // Final cleanup
    let finalResult = accumulatedResult;
    if (finalResult.startsWith('"')) {
      finalResult = finalResult.slice(1);
    }
    if (finalResult.endsWith('"')) {
      finalResult = finalResult.slice(0, -1);
    }
    finalResult = finalResult.replace(/\\n/g, '\n');
    
    setMessages((prev) =>
      prev.map((msg) =>
        msg.id === assistantMessageId
          ? { ...msg, content: finalResult, isStreaming: false }
          : msg
      )
    );
  };

  const handleSubmitXiaoxiongAgent = async (userMessage: Message, assistantMessageId: string) => {
    const userId = getUserId();
    const requestStartTime = Date.now();
    console.log(`[前端-Agent] ⏱️ 开始发送请求: ${new Date().toISOString()}`);
    
    const response = await fetch(`${SUPABASE_URL}/functions/v1/xiaoxiong-agent`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${SUPABASE_ANON_KEY}`,
        'apikey': SUPABASE_ANON_KEY,
      },
      body: JSON.stringify({ 
        message: userMessage.content, 
        userId,
      }),
      signal: abortControllerRef.current?.signal,
    });

    const responseTime = Date.now() - requestStartTime;
    console.log(`[前端-Agent] ⏱️ 收到响应头: ${new Date().toISOString()}, 耗时: ${responseTime}ms`);

    if (!response.ok) {
      throw new Error(`Request failed with status: ${response.status}`);
    }

    if (!response.body) {
      throw new Error('No response body');
    }

    const reader = response.body.getReader();
    const decoder = new TextDecoder();
    let accumulatedResult = "";
    let firstChunkReceived = false;
    let chunkCount = 0;

    while (true) {
      const { done, value } = await reader.read();
      
      if (done) {
        console.log(`[前端-Agent] ⏱️ 流结束: ${new Date().toISOString()}, 总耗时: ${Date.now() - requestStartTime}ms, 共 ${chunkCount} 个数据块`);
        break;
      }

      chunkCount++;
      const chunk = decoder.decode(value, { stream: true });
      accumulatedResult += chunk;
      
      if (!firstChunkReceived) {
        firstChunkReceived = true;
        const ttfb = Date.now() - requestStartTime;
        console.log(`[前端-Agent] ⏱️ 首个数据块 (TTFB): ${new Date().toISOString()}, 耗时: ${ttfb}ms, 大小: ${chunk.length} 字符`);
      }
      
      // 使用 flushSync 强制同步渲染，绕过 React 18 批量更新
      flushSync(() => {
        setMessages((prev) =>
          prev.map((msg) =>
            msg.id === assistantMessageId
              ? { ...msg, content: accumulatedResult }
              : msg
          )
        );
      });
      
      // 等待浏览器渲染帧，确保每个字符都能显示
      await new Promise(resolve => requestAnimationFrame(resolve));
    }

    // Mark streaming as complete
    setMessages((prev) =>
      prev.map((msg) =>
        msg.id === assistantMessageId
          ? { ...msg, isStreaming: false }
          : msg
      )
    );
  };

  const handleSubmitLovableAI = async (userMessage: Message, assistantMessageId: string) => {
    const requestStartTime = Date.now();
    console.log(`[前端-LovableAI] ⏱️ 开始发送请求: ${new Date().toISOString()}`);
    
    const response = await fetch(`${SUPABASE_URL}/functions/v1/lovable-chat`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${SUPABASE_ANON_KEY}`,
        'apikey': SUPABASE_ANON_KEY,
      },
      body: JSON.stringify({ 
        message: userMessage.content,
        lat: userLocation?.lat,
        lng: userLocation?.lng,
        language: i18n.language,
      }),
      signal: abortControllerRef.current?.signal,
    });

    const responseTime = Date.now() - requestStartTime;
    console.log(`[前端-LovableAI] ⏱️ 收到响应头: ${new Date().toISOString()}, 耗时: ${responseTime}ms`);

    if (!response.ok) {
      const errorData = await response.json().catch(() => ({}));
      throw new Error(errorData.error || `Request failed with status: ${response.status}`);
    }

    if (!response.body) {
      throw new Error('No response body');
    }

    const reader = response.body.getReader();
    const decoder = new TextDecoder();
    let accumulatedResult = "";
    let textBuffer = "";
    let firstChunkReceived = false;

    while (true) {
      const { done, value } = await reader.read();
      
      if (done) {
        console.log(`[前端-LovableAI] ⏱️ 流结束: ${new Date().toISOString()}, 总耗时: ${Date.now() - requestStartTime}ms`);
        break;
      }

      textBuffer += decoder.decode(value, { stream: true });

      if (!firstChunkReceived) {
        firstChunkReceived = true;
        const ttfb = Date.now() - requestStartTime;
        console.log(`[前端-LovableAI] ⏱️ 首个数据块 (TTFB): ${new Date().toISOString()}, 耗时: ${ttfb}ms`);
      }

      // Parse SSE lines
      let newlineIndex: number;
      while ((newlineIndex = textBuffer.indexOf("\n")) !== -1) {
        let line = textBuffer.slice(0, newlineIndex);
        textBuffer = textBuffer.slice(newlineIndex + 1);

        if (line.endsWith("\r")) line = line.slice(0, -1);
        if (line.startsWith(":") || line.trim() === "") continue;
        if (!line.startsWith("data: ")) continue;

        const jsonStr = line.slice(6).trim();
        if (jsonStr === "[DONE]") break;

        try {
          const parsed = JSON.parse(jsonStr);
          const content = parsed.choices?.[0]?.delta?.content as string | undefined;
          if (content) {
            accumulatedResult += content;
            
            // Use flushSync for immediate rendering
            flushSync(() => {
              setMessages((prev) =>
                prev.map((msg) =>
                  msg.id === assistantMessageId
                    ? { ...msg, content: accumulatedResult }
                    : msg
                )
              );
            });
            
            await new Promise(resolve => requestAnimationFrame(resolve));
          }
        } catch {
          // 忽略解析失败的行，继续处理下一行
        }
      }
    }

    // Mark streaming as complete
    setMessages((prev) =>
      prev.map((msg) =>
        msg.id === assistantMessageId
          ? { ...msg, isStreaming: false }
          : msg
      )
    );
  };

  // Check if message needs location info
  const needsLocationInfo = (message: string): boolean => {
    const lowerMessage = message.toLowerCase();
    return LOCATION_KEYWORDS.some(keyword => lowerMessage.includes(keyword));
  };

  // Process and send message to AI
  const processMessage = async (messageContent: string) => {
    const userMessage: Message = {
      id: Date.now().toString(),
      role: "user",
      content: messageContent,
    };

    const assistantMessageId = (Date.now() + 1).toString();
    const assistantMessage: Message = {
      id: assistantMessageId,
      role: "assistant",
      content: "",
      isStreaming: true,
    };

    setMessages((prev) => [...prev, userMessage, assistantMessage]);
    setInput("");
    setIsLoading(true);

    // Cancel any ongoing request
    if (abortControllerRef.current) {
      abortControllerRef.current.abort();
    }
    abortControllerRef.current = new AbortController();

    try {
      if (provider === "lovable-ai") {
        await handleSubmitLovableAI(userMessage, assistantMessageId);
      } else if (provider === "xiaoxiong-agent") {
        await handleSubmitXiaoxiongAgent(userMessage, assistantMessageId);
      } else {
        await handleSubmitAskXiaoxiong(userMessage, assistantMessageId);
      }
    } catch (err) {
      if (err instanceof Error && err.name === 'AbortError') {
        console.log('Request aborted');
        return;
      }
      console.error("Ask API error:", err);
      const errorMessage =
        i18n.language === "zh"
          ? "抱歉，暂时无法获取结果，请稍后再试"
          : i18n.language === "pt"
          ? "Desculpe, não foi possível obter resultados. Tente novamente mais tarde."
          : "Sorry, unable to get results. Please try again later.";
      
      setMessages((prev) =>
        prev.map((msg) =>
          msg.id === assistantMessageId
            ? { ...msg, content: errorMessage, isStreaming: false }
            : msg
        )
      );
    } finally {
      setIsLoading(false);
    }
  };

  // Handle location permission confirmation
  const handleLocationConfirm = () => {
    setShowLocationDialog(false);
    navigator.geolocation?.getCurrentPosition(
      (position) => {
        setUserLocation({
          lat: position.coords.latitude,
          lng: position.coords.longitude,
        });
        // Process pending message after getting location
        if (pendingMessage) {
          setTimeout(() => {
            processMessage(pendingMessage);
            setPendingMessage(null);
          }, 100);
        }
      },
      (error) => {
        console.log("Location permission denied:", error.message);
        // Still process message even without location
        if (pendingMessage) {
          processMessage(pendingMessage);
          setPendingMessage(null);
        }
      },
      { enableHighAccuracy: true, timeout: 10000, maximumAge: 0 }
    );
  };

  const handleSubmit = async () => {
    if (!input.trim() || isLoading) return;

    const messageContent = input.trim();

    // Check if location is needed but not available
    if (!userLocation && needsLocationInfo(messageContent)) {
      setPendingMessage(messageContent);
      setShowLocationDialog(true);
      setInput("");
      return;
    }

    await processMessage(messageContent);
  };

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault();
      handleSubmit();
    }
  };

  // Voice input handling
  const startListening = () => {
    // Prefer native voice bridge if available (in native apps)
    if (nativeVoiceSupported) {
      NativeVoice.start();
      setIsListening(true);
      return;
    }

    // Fallback to Web Speech API
    const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
    if (!SpeechRecognition) {
      console.log("Speech recognition not supported");
      return;
    }

    // Stop any existing recognition
    if (recognitionRef.current) {
      recognitionRef.current.stop();
    }

    const recognition = new SpeechRecognition();
    recognitionRef.current = recognition;

    // Set language based on current i18n language
    const langMap: Record<string, string> = {
      zh: "zh-CN",
      en: "en-US",
      pt: "pt-BR",
    };
    recognition.lang = langMap[i18n.language] || "zh-CN";
    recognition.continuous = false;
    recognition.interimResults = true;

    recognition.onstart = () => {
      setIsListening(true);
    };

    recognition.onresult = (event) => {
      const transcript = Array.from(event.results)
        .map((result) => result[0].transcript)
        .join("");
      setInput(transcript);
    };

    recognition.onerror = (event) => {
      console.log("Speech recognition error:", event.error);
      setIsListening(false);
    };

    recognition.onend = () => {
      setIsListening(false);
    };

    recognition.start();
  };

  const stopListening = () => {
    // Stop native voice if using native bridge
    if (nativeVoiceSupported) {
      NativeVoice.stop();
      setIsListening(false);
      return;
    }
    
    // Stop Web Speech API
    if (recognitionRef.current) {
      recognitionRef.current.stop();
      setIsListening(false);
    }
  };

  const toggleListening = () => {
    if (isListening) {
      stopListening();
    } else {
      startListening();
    }
  };

  const handleClose = () => {
    if (abortControllerRef.current) {
      abortControllerRef.current.abort();
    }
    onOpenChange(false);
  };

  const handleLinkClick = (href: string) => {
    handleClose();
    navigate(href);
  };

  // Handle external link click - show confirmation dialog
  const handleExternalLinkClick = (url: string) => {
    setPendingExternalLink(url);
    setExternalLinkDialogOpen(true);
  };

  // Confirm external link navigation
  const confirmExternalLink = () => {
    if (pendingExternalLink) {
      openExternalUrl(pendingExternalLink);
    }
    setExternalLinkDialogOpen(false);
    setPendingExternalLink(null);
  };

  // Parse text with links: [text](/path), [text](!external:url) or [text](url)
  const parseTextWithLinks = (text: string): JSX.Element[] => {
    const linkRegex = /\[([^\]]+)\]\(([^)]+)\)/g;
    const parts: JSX.Element[] = [];
    let lastIndex = 0;
    let match;
    let keyIndex = 0;

    while ((match = linkRegex.exec(text)) !== null) {
      if (match.index > lastIndex) {
        parts.push(<span key={keyIndex++}>{text.slice(lastIndex, match.index)}</span>);
      }

      const linkText = match[1];
      const href = match[2];

      if (href.startsWith("/")) {
        // Internal link - direct navigation
        parts.push(
          <button
            key={keyIndex++}
            onClick={() => handleLinkClick(href)}
            className="text-primary hover:underline font-medium cursor-pointer"
          >
            {linkText}
          </button>
        );
      } else if (href.startsWith("!external:")) {
        // External link with !external: prefix - show confirmation dialog
        const actualUrl = href.slice("!external:".length);
        parts.push(
          <button
            key={keyIndex++}
            onClick={() => handleExternalLinkClick(actualUrl)}
            className="text-primary hover:underline font-medium cursor-pointer"
          >
            {linkText}
          </button>
        );
      } else if (href.startsWith("http://") || href.startsWith("https://")) {
        // Regular external URL - also show confirmation dialog for iOS compatibility
        parts.push(
          <button
            key={keyIndex++}
            onClick={() => handleExternalLinkClick(href)}
            className="text-primary hover:underline font-medium cursor-pointer"
          >
            {linkText}
          </button>
        );
      } else {
        // Other links - render as regular anchor (unlikely case)
        parts.push(
          <a
            key={keyIndex++}
            href={href}
            target="_blank"
            rel="noopener noreferrer"
            className="text-primary hover:underline font-medium"
          >
            {linkText}
          </a>
        );
      }

      lastIndex = match.index + match[0].length;
    }

    if (lastIndex < text.length) {
      parts.push(<span key={keyIndex++}>{text.slice(lastIndex)}</span>);
    }

    return parts.length > 0 ? parts : [<span key="0">{text}</span>];
  };

  // Remove bold markers from text
  const removeBoldMarkers = (text: string): string => {
    return text.replace(/\*\*(.*?)\*\*/g, '$1');
  };

  // Truncate text if too long
  const truncateText = (text: string, maxLength: number = 20): string => {
    if (text.length <= maxLength) return text;
    return text.slice(0, maxLength) + '...';
  };

  // Render a markdown table
  const renderTable = (tableLines: string[], key: number) => {
    const parseRow = (line: string) =>
      line
        .split("|")
        .slice(1, -1)
        .map((cell) => removeBoldMarkers(cell.trim()));

    const headers = parseRow(tableLines[0]);
    // Skip separator line (index 1), parse data rows
    const rows = tableLines.slice(2).map(parseRow);

    return (
      <div key={key} className="overflow-x-auto my-2 max-w-full">
        <table className="w-full text-xs border border-border rounded-md overflow-hidden table-fixed">
          <thead className="bg-muted/60">
            <tr>
              {headers.map((header, i) => (
                <th
                  key={i}
                  className="px-2 py-2 text-left font-semibold border-b border-border truncate max-w-[100px]"
                  title={header}
                >
                  {parseTextWithLinks(truncateText(header, 15))}
                </th>
              ))}
            </tr>
          </thead>
          <tbody>
            {rows.map((row, rowIndex) => (
              <tr
                key={rowIndex}
                className="border-b border-border last:border-0 hover:bg-muted/30 transition-colors"
              >
                {row.map((cell, cellIndex) => (
                  <td 
                    key={cellIndex} 
                    className="px-2 py-2 truncate max-w-[100px]"
                    title={cell}
                  >
                    {parseTextWithLinks(truncateText(cell, 18))}
                  </td>
                ))}
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  };

  const renderMarkdown = (content: string) => {
    const lines = content.split("\n");
    const elements: JSX.Element[] = [];
    let i = 0;

    while (i < lines.length) {
      const line = lines[i];

      // Detect table: starts with | and contains multiple |
      if (line.trim().startsWith("|") && line.split("|").length > 2) {
        const tableLines: string[] = [];

        // Collect all consecutive table lines
        while (i < lines.length && lines[i].trim().startsWith("|")) {
          tableLines.push(lines[i]);
          i++;
        }

        // Need at least header + separator + 1 data row, but render even with just header + separator
        if (tableLines.length >= 2) {
          elements.push(renderTable(tableLines, elements.length));
        }
        continue;
      }

      // Existing markdown rendering logic
      if (line.startsWith("### ")) {
        elements.push(
          <h3 key={elements.length} className="text-base font-semibold mt-3 mb-1">
            {parseTextWithLinks(line.slice(4))}
          </h3>
        );
      } else if (line.startsWith("## ")) {
        elements.push(
          <h2 key={elements.length} className="text-lg font-bold mt-4 mb-2">
            {parseTextWithLinks(line.slice(3))}
          </h2>
        );
      } else if (line.startsWith("# ")) {
        elements.push(
          <h1 key={elements.length} className="text-xl font-bold mt-4 mb-2">
            {parseTextWithLinks(line.slice(2))}
          </h1>
        );
      } else if (line.includes("**")) {
        const parts = line.split(/\*\*(.*?)\*\*/g);
        elements.push(
          <p key={elements.length} className="mb-1 leading-relaxed">
            {parts.map((part, idx) =>
              idx % 2 === 1 ? (
                <strong key={idx} className="font-semibold">
                  {parseTextWithLinks(part)}
                </strong>
              ) : (
                parseTextWithLinks(part)
              )
            )}
          </p>
        );
      } else if (line.startsWith("- ") || line.startsWith("* ")) {
        elements.push(
          <li key={elements.length} className="ml-4 mb-0.5 list-disc">
            {parseTextWithLinks(line.slice(2))}
          </li>
        );
      } else if (/^\d+\.\s/.test(line)) {
        elements.push(
          <li key={elements.length} className="ml-4 mb-0.5 list-decimal">
            {parseTextWithLinks(line.replace(/^\d+\.\s/, ""))}
          </li>
        );
      } else if (line.match(/^[-*_]{3,}$/)) {
        elements.push(<hr key={elements.length} className="my-3 border-border" />);
      } else if (line.trim() === "") {
        elements.push(<div key={elements.length} className="h-1" />);
      } else {
        elements.push(
          <p key={elements.length} className="mb-1 leading-relaxed">
            {parseTextWithLinks(line)}
          </p>
        );
      }

      i++;
    }

    return elements;
  };

  const getTitle = () => {
    if (i18n.language === "zh") return "小熊AI助手";
    if (i18n.language === "pt") return "Assistente IA Xiaoxiong";
    return "Xiaoxiong AI Assistant";
  };

  const getPlaceholder = () => {
    if (i18n.language === "zh") return "告诉小熊，你想吃什么...";
    if (i18n.language === "pt") return "Diga ao Xiaoxiong o que você quer comer...";
    return "Tell Xiaoxiong what you want to eat...";
  };

  const getWelcomeMessage = () => {
    if (i18n.language === "zh") return "你好，我是小熊，我来帮你推荐餐厅；比如：「我附近的寿司餐厅」";
    if (i18n.language === "pt") return "Olá, eu sou o Xiaoxiong, vou te ajudar a encontrar restaurantes; por exemplo: 「restaurantes de sushi perto de mim」";
    return "Hi, I'm Xiaoxiong, I'll help you find restaurants; for example: \"sushi restaurants near me\"";
  };

  return (
    <Dialog open={open} onOpenChange={handleClose}>
      <DialogContent className="sm:max-w-2xl sm:h-[90vh] sm:max-h-[90vh] flex flex-col p-0 gap-0 overflow-hidden">
        <DialogHeader className="px-4 py-3 shrink-0 border-b">
          <DialogTitle className="text-base font-semibold flex items-center gap-2">
            {logoUrl && <img src={logoUrl} alt="Logo" className="w-8 h-8 rounded-full object-cover" />}
            {getTitle()}
          </DialogTitle>
        </DialogHeader>

          {/* Chat Messages Area - Scrollable, min-h-0 ensures flex shrinking works on iOS */}
          <ScrollArea className="flex-1 min-h-0 overflow-hidden" ref={scrollAreaRef}>
            <div className="p-4 space-y-4">
              {/* Welcome message */}
              {messages.length === 0 && (
                <div className="flex gap-3">
                  <div className="w-8 h-8 rounded-full bg-muted flex items-center justify-center shrink-0 overflow-hidden">
                    {logoUrl && <img src={logoUrl} alt="Xiaoxiong" className="w-full h-full object-cover" />}
                  </div>
                  <div className="flex-1 bg-muted rounded-lg px-3 py-2 text-sm">
                    {getWelcomeMessage()}
                  </div>
                </div>
              )}

              {/* Messages */}
              {messages.map((message) => (
                <div
                  key={message.id}
                  className={cn(
                    "flex gap-3",
                    message.role === "user" && "flex-row-reverse"
                  )}
                >
                  {message.role === "user" ? (
                    <div className="w-8 h-8 rounded-full bg-muted flex items-center justify-center shrink-0">
                      <User className="h-4 w-4" />
                    </div>
                  ) : (
                    <div className="w-8 h-8 rounded-full bg-muted flex items-center justify-center shrink-0 overflow-hidden">
                      {logoUrl && <img src={logoUrl} alt="Xiaoxiong" className="w-full h-full object-cover" />}
                    </div>
                  )}
                  <div
                    className={cn(
                      "flex-1 max-w-[85%] rounded-lg px-3 py-2 text-sm",
                      message.role === "user"
                        ? "bg-primary text-primary-foreground"
                        : "bg-muted"
                    )}
                  >
                    {message.role === "assistant" ? (
                      <>
                        {message.content ? (
                          <div className="prose-sm">
                            {renderMarkdown(message.content)}
                          </div>
                        ) : (
                          <div className="flex items-center gap-2 text-muted-foreground">
                            <Loader2 className="h-4 w-4 animate-spin" />
                            <span>
                              {i18n.language === "zh"
                                ? "思考中..."
                                : i18n.language === "pt"
                                ? "Pensando..."
                                : "Thinking..."}
                            </span>
                          </div>
                        )}
                        {message.isStreaming && message.content && (
                          <span className="inline-block w-1.5 h-4 bg-primary/50 animate-pulse ml-0.5 align-middle" />
                        )}
                      </>
                    ) : (
                      message.content
                    )}
                  </div>
                </div>
              ))}
              <div ref={messagesEndRef} />
            </div>
          </ScrollArea>

          {/* Input Section - Glassmorphism floating bar */}
          <div className="px-2 py-1.5 pb-[max(1.5rem,calc(env(safe-area-inset-bottom)+1rem))] md:pb-4 shrink-0">
            <div className="flex items-center gap-1.5 px-2 py-1.5 rounded-full backdrop-blur-md border border-border/50 bg-muted/80 dark:bg-muted/60">
              {/* Voice Input Button */}
              {shouldShowVoiceInput() && (
                <Button
                  variant="ghost"
                  size="icon"
                  onClick={toggleListening}
                  disabled={isLoading}
                  className={cn("bg-transparent hover:bg-transparent", isListening && "text-destructive")}
                >
                  {isListening ? <MicOff className="h-4 w-4" /> : <Mic className="h-4 w-4" />}
                </Button>
              )}
              
              <Input
                value={input}
                onChange={(e) => setInput(e.target.value)}
                onKeyDown={handleKeyDown}
                placeholder={isListening ? (i18n.language === "zh" ? "正在聆听..." : "Listening...") : getPlaceholder()}
                disabled={isLoading}
                className="flex-1 bg-transparent border-0 shadow-none focus-visible:ring-0 focus-visible:ring-offset-0"
              />
              <Button
                onClick={handleSubmit}
                disabled={!input.trim() || isLoading}
                size="icon"
                variant="ghost"
                className="bg-transparent hover:bg-white/50 rounded-full"
              >
                {isLoading ? <Loader2 className="h-4 w-4 animate-spin" /> : <Send className="h-4 w-4" />}
              </Button>
            </div>
          </div>

          {/* Location Permission Dialog */}
          <LocationPermissionDialog
            open={showLocationDialog}
            onOpenChange={(open) => {
              setShowLocationDialog(open);
              if (!open && pendingMessage) {
                // User cancelled, still process message without location
                processMessage(pendingMessage);
                setPendingMessage(null);
              }
            }}
            onConfirm={handleLocationConfirm}
          />

          {/* External Link Confirm Dialog */}
          <ExternalLinkConfirmDialog
            open={externalLinkDialogOpen}
            onOpenChange={setExternalLinkDialogOpen}
            onConfirm={confirmExternalLink}
            url={pendingExternalLink || undefined}
          />
      </DialogContent>
    </Dialog>
  );
};

export default AskXiaoxiongDialog;
