import { useMemo } from "react";
import { useActivities, Activity, ActivityCreator } from "@/hooks/useActivities";
import { addDays, isSameDay, getDay, getDate, getMonth, startOfDay } from "date-fns";

// Extended Activity type with creator
type ActivityWithExtras = Activity & { creator: ActivityCreator; share_count?: number };

interface DateRange {
  start: number;
  end: number;
}

// Type guard to check if value is a valid DateRange array
const isDateRangeArray = (value: unknown): value is DateRange[] => {
  if (!value || !Array.isArray(value)) return false;
  return value.every(item => 
    typeof item === 'object' && 
    item !== null &&
    'start' in item && 
    'end' in item &&
    typeof item.start === 'number' &&
    typeof item.end === 'number'
  );
};

// Check if activity is valid on a specific date
const isActivityValidOnDate = (activity: ActivityWithExtras, targetDate: Date): boolean => {
  const startDate = startOfDay(new Date(activity.start_date));
  const endDate = activity.end_date ? startOfDay(new Date(activity.end_date)) : null;
  const target = startOfDay(targetDate);
  
  // Check if within valid period
  if (target < startDate) return false;
  if (endDate && target > endDate) return false;
  
  const repeatMode = activity.repeat_mode || "none";
  
  switch (repeatMode) {
    case "none":
      // Only valid on start date
      return isSameDay(startDate, target);
      
    case "daily":
      // Valid every day
      return true;
      
    case "weekly":
      // Check weekday (0-6, Sunday = 0)
      const targetWeekday = getDay(target);
      // Need to fetch repeat_weekday from the raw data
      const weekday = (activity as any).repeat_weekday;
      return weekday === targetWeekday;
      
    case "monthly":
      // Check day of month ranges
      const targetDayOfMonth = getDate(target);
      const monthRanges = (activity as any).repeat_month_day;
      if (!isDateRangeArray(monthRanges)) return false;
      return monthRanges.some(range => 
        targetDayOfMonth >= range.start && targetDayOfMonth <= range.end
      );
      
    case "yearly":
      // Check month and day of year
      const targetMonth = getMonth(target) + 1; // 1-12
      const targetDay = getDate(target);
      const yearlyMonth = (activity as any).repeat_yearly_month;
      const yearlyDay = (activity as any).repeat_yearly_day;
      return yearlyMonth === targetMonth && yearlyDay === targetDay;
             
    default:
      return false;
  }
};

// Get sort weight for activity (lower = higher priority)
const getActivitySortWeight = (activity: ActivityWithExtras): number => {
  const repeatMode = activity.repeat_mode || "none";
  switch (repeatMode) {
    case "none": return 0;      // Dated activities first
    case "weekly": return 1;
    case "monthly": return 2;
    case "yearly": return 3;
    case "daily": return 4;     // Daily activities last
    default: return 5;
  }
};

export interface ActivityWithDayLabel extends ActivityWithExtras {
  dayLabel: "today" | "tomorrow" | "dayAfterTomorrow";
}

export const useRecentActivities = () => {
  const { data: activities, isLoading } = useActivities();
  
  const recentActivities = useMemo(() => {
    if (!activities) return { today: [], tomorrow: [], dayAfterTomorrow: [] };
    
    const today = new Date();
    const tomorrow = addDays(today, 1);
    const dayAfterTomorrow = addDays(today, 2);
    
    const filterAndSort = (date: Date): ActivityWithExtras[] => {
      return activities
        .filter(a => a.visible && a.status === "published")
        .filter(a => isActivityValidOnDate(a, date))
        .sort((a, b) => {
          // Sort by type weight first
          const weightDiff = getActivitySortWeight(a) - getActivitySortWeight(b);
          if (weightDiff !== 0) return weightDiff;
          
          // Then by pinned and sort_order
          if (a.pinned !== b.pinned) return a.pinned ? -1 : 1;
          return (a.sort_order || 0) - (b.sort_order || 0);
        });
    };
    
    return {
      today: filterAndSort(today),
      tomorrow: filterAndSort(tomorrow),
      dayAfterTomorrow: filterAndSort(dayAfterTomorrow),
    };
  }, [activities]);
  
  // Combine all recent activities (deduplicated, with day label)
  const allRecentActivities = useMemo((): ActivityWithDayLabel[] => {
    const seen = new Set<string>();
    const combined: ActivityWithDayLabel[] = [];
    
    // Add today's activities
    recentActivities.today.forEach(activity => {
      if (!seen.has(activity.id)) {
        seen.add(activity.id);
        combined.push({ ...activity, dayLabel: "today" });
      }
    });
    
    // Add tomorrow's activities
    recentActivities.tomorrow.forEach(activity => {
      if (!seen.has(activity.id)) {
        seen.add(activity.id);
        combined.push({ ...activity, dayLabel: "tomorrow" });
      }
    });
    
    // Add day after tomorrow's activities
    recentActivities.dayAfterTomorrow.forEach(activity => {
      if (!seen.has(activity.id)) {
        seen.add(activity.id);
        combined.push({ ...activity, dayLabel: "dayAfterTomorrow" });
      }
    });
    
    // Sort combined list
    return combined.sort((a, b) => {
      const weightDiff = getActivitySortWeight(a) - getActivitySortWeight(b);
      if (weightDiff !== 0) return weightDiff;
      if (a.pinned !== b.pinned) return a.pinned ? -1 : 1;
      return (a.sort_order || 0) - (b.sort_order || 0);
    });
  }, [recentActivities]);
  
  return {
    recentActivities,
    allRecentActivities,
    isLoading,
    hasActivities: allRecentActivities.length > 0,
  };
};
