import { memo, useMemo, useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Link, useNavigate } from "react-router-dom";
import { ArrowRight, Calendar, UtensilsCrossed, ShoppingBag } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Skeleton } from "@/components/ui/skeleton";
import { useRecentActivities, ActivityWithDayLabel } from "@/hooks/useRecentActivities";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";
import { cn } from "@/lib/utils";

// Get responsive display count based on window width
const useResponsiveCount = () => {
  const [count, setCount] = useState(4);
  
  useEffect(() => {
    const updateCount = () => {
      const width = window.innerWidth;
      if (width < 640) {
        setCount(2);
      } else if (width < 1024) {
        setCount(3);
      } else {
        setCount(4);
      }
    };
    
    updateCount();
    window.addEventListener("resize", updateCount);
    return () => window.removeEventListener("resize", updateCount);
  }, []);
  
  return count;
};

// Activity Card Component
const ActivityCard = memo(({ 
  activity, 
  dayLabel 
}: { 
  activity: ActivityWithDayLabel; 
  dayLabel: string;
}) => {
  const navigate = useNavigate();
  const { t } = useTranslation();
  const { getLocalizedField } = useLocalizedContent();
  
  const title = getLocalizedField(activity, "title");
  
  const serviceTypeLabel = useMemo(() => {
    switch (activity.service_type) {
      case "dine_in":
        return t("activities.serviceType.dine_in");
      case "takeaway":
        return t("activities.serviceType.takeaway");
      case "both":
        return t("activities.serviceType.both");
      default:
        return "";
    }
  }, [activity.service_type, t]);
  
  const handleClick = () => {
    navigate(`/activity/${activity.id}`);
  };
  
  return (
    <Card 
      className="group cursor-pointer overflow-hidden hover:shadow-lg transition-all duration-300 border-border/50 bg-card"
      onClick={handleClick}
    >
      {/* Image Container */}
      <div className="relative aspect-[16/10] overflow-hidden bg-muted">
        {activity.image_url ? (
          <img 
            src={activity.image_url} 
            alt={title}
            className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
            loading="lazy"
          />
        ) : (
          <div className="w-full h-full flex items-center justify-center bg-gradient-to-br from-primary/20 to-primary/5">
            <Calendar className="w-12 h-12 text-primary/40" />
          </div>
        )}
        
        {/* Day Badge */}
        <Badge 
          className={cn(
            "absolute top-2 left-2 text-xs font-medium",
            dayLabel === t("home.recentActivities.today") 
              ? "bg-primary text-primary-foreground" 
              : "bg-secondary text-secondary-foreground"
          )}
        >
          {dayLabel}
        </Badge>
        
        {/* Pinned Badge */}
        {activity.pinned && (
          <Badge 
            className="absolute top-2 right-2 text-xs bg-accent text-accent-foreground"
          >
            {t("activities.pinned")}
          </Badge>
        )}
      </div>
      
      {/* Content */}
      <div className="p-3 space-y-2">
        <h3 className="font-medium text-sm leading-tight line-clamp-2 text-foreground group-hover:text-primary transition-colors">
          {title}
        </h3>
        
        <div className="flex items-center gap-1.5 text-xs text-muted-foreground">
          {activity.service_type === "dine_in" && (
            <UtensilsCrossed className="w-3 h-3 flex-shrink-0" />
          )}
          {activity.service_type === "takeaway" && (
            <ShoppingBag className="w-3 h-3 flex-shrink-0" />
          )}
          {activity.service_type === "both" && (
            <>
              <UtensilsCrossed className="w-3 h-3 flex-shrink-0" />
              <ShoppingBag className="w-3 h-3 flex-shrink-0" />
            </>
          )}
          <span className="truncate">{serviceTypeLabel}</span>
        </div>
      </div>
    </Card>
  );
});
ActivityCard.displayName = "ActivityCard";

// Loading Skeleton
const LoadingSkeleton = memo(() => {
  const displayCount = useResponsiveCount();
  
  return (
    <div className="grid grid-cols-2 sm:grid-cols-3 lg:grid-cols-4 gap-4">
      {Array.from({ length: displayCount }).map((_, i) => (
        <Card key={i} className="overflow-hidden">
          <Skeleton className="aspect-[16/10] w-full" />
          <div className="p-3 space-y-2">
            <Skeleton className="h-4 w-full" />
            <Skeleton className="h-3 w-2/3" />
          </div>
        </Card>
      ))}
    </div>
  );
});
LoadingSkeleton.displayName = "LoadingSkeleton";

// Main Component
const RecentActivitiesSection = memo(() => {
  const { t } = useTranslation();
  const { allRecentActivities, isLoading, hasActivities } = useRecentActivities();
  const displayCount = useResponsiveCount();
  const [showAll, setShowAll] = useState(false);
  
  // Get day label for display
  const getDayLabel = (dayKey: "today" | "tomorrow" | "dayAfterTomorrow") => {
    switch (dayKey) {
      case "today":
        return t("home.recentActivities.today");
      case "tomorrow":
        return t("home.recentActivities.tomorrow");
      case "dayAfterTomorrow":
        return t("home.recentActivities.dayAfterTomorrow");
      default:
        return "";
    }
  };
  
  // Slice activities to display count or show all
  const displayedActivities = useMemo(() => {
    if (showAll) {
      return allRecentActivities;
    }
    return allRecentActivities.slice(0, displayCount);
  }, [allRecentActivities, displayCount, showAll]);
  
  const hasMore = allRecentActivities.length > displayCount;
  
  // Don't render if no activities and not loading
  if (!isLoading && !hasActivities) {
    return null;
  }
  
  return (
    <section className="py-8 sm:py-12 px-4 bg-secondary/20">
      <div className="container mx-auto max-w-7xl">
        {/* Header */}
        <div className="flex items-center justify-between mb-6">
          <h2 className="text-xl sm:text-2xl font-bold text-foreground">
            {t("home.recentActivities.title")}
          </h2>
          
          {hasMore && !showAll && (
            <Button 
              variant="ghost" 
              size="sm" 
              className="text-primary hover:text-primary/80"
              onClick={() => setShowAll(true)}
            >
              <span className="flex items-center gap-1">
                {t("home.recentActivities.viewAll")}
                <ArrowRight className="w-4 h-4" />
              </span>
            </Button>
          )}
          
          {showAll && (
            <Button 
              variant="ghost" 
              size="sm" 
              className="text-muted-foreground hover:text-foreground"
              onClick={() => setShowAll(false)}
            >
              <span className="flex items-center gap-1">
                {t("home.recentActivities.collapse")}
              </span>
            </Button>
          )}
        </div>
        
        {/* Content */}
        {isLoading ? (
          <LoadingSkeleton />
        ) : (
          <div className="grid grid-cols-2 sm:grid-cols-3 lg:grid-cols-4 gap-4">
            {displayedActivities.map((activity) => (
              <ActivityCard 
                key={activity.id} 
                activity={activity}
                dayLabel={getDayLabel(activity.dayLabel)}
              />
            ))}
          </div>
        )}
      </div>
    </section>
  );
});
RecentActivitiesSection.displayName = "RecentActivitiesSection";

export default RecentActivitiesSection;
